<script>
import { Player, Video, Hls } from '@vime/vue-next'

export default {
  name: 'App',
  components: {
    Player,
    Video,
    Hls
  },
  data() {  
    return {  
      playurl: '',
      show: {
        hls: '',
        mp4: '',
      },
      m3u8List: [],  
      currentVideo: null,  
    };  
  },

  mounted() {
    var id = this.getUrlParam('id') || ''
    // console.log("id", id);
    this.getData(id);
  },

  methods: {
    async getData(id) {
      // console.log("id", id)
      if(id.length > 5) {
        const response = await fetch("/hls/" + id);
        const data = await response.json();
        this.m3u8List = data;
        this.playurl = this.m3u8List[0]
        this.playByUrl(this.playurl)
      }
    },

    onchangePlay(item) {
      console.log("item", item);
    },

    playByUrl(url) {
      // console.log("url", url)
      if(url.indexOf('.m3u8') !== -1) {
        this.show.hls = true
        this.show.mp4 = false
      } else {
        this.show.hls = false
        this.show.mp4 = true
      }
      this.$refs.player.play()
    },
    
    getUrlParam (e) {
      const reg = new RegExp('(^|&)' + e + '=([^&]*)(&|$)', 'i')
      const r = window.location.search.substr(1).match(reg)
      if (r !== null) return decodeURI(r[2])
      return null
    },
  }
}
</script>

